@import "../../common/styles/sass/variables";

.nav {
  padding-right: 50px;
  padding-left: 50px;
  & a {
    text-decoration: none;
    color: #9f9f9f;
    margin-right: 30px;
    &:hover {
      color: $primaryColor;
      cursor: pointer;
    }
    &:last-child {
      margin: 0;
    }
  }
  @media screen and (max-width: 400px) {
    display: none;
  }
}

